const helpers = () => {
  /**
   * @name isContextSelected
   * @type {function}
   * @description
   * Checks if all necessary properties are defined for selecting a context
   *
   * @param {object} state Current state
   * @returns {Boolean}
   */
  const isContextSelected = (state) => (
    state.serviceAgreementId &&
    state.serviceAgreementName &&
    state.legalEntityId &&
    state.legalEntityName
  );

  return {
    isContextSelected,
  };
};

export default helpers;



